import React, { useState } from "react";
import { Modal, Button, message, Form, Input, Typography, Select } from "antd";

import {
  ADD_DESIGNATION,
  DESIGNATION_FORM_FIELDS,
  // DESIGNATION_NAME,
  // DESIGNATION_NAME_MESSAGE,
  SUCCESS_MESSAGE,
  UPDATE_DESIGNATION,
  UPDATE_SUCCESS,
} from "constants/ManageDesinations";
import {
  // ADD_TEXT,
  CONFIRM_MESSAGE,
  // SUBMIT_TEXT,
  FORM_NAME,
  // MODAL_BACK_BUTTON,
  VALIDATION_FAILED,
  // CANCEL_TEXT,
  // MODAL_ADD_BUTTON_TYPE,
  FORM_LAYOUT,
  FILTER_FORM_FIELDS_OF_DESIGNATION,
  ADD_TEXT,
} from "constants/CommonConstants";
import designationService from "services/DesignationService";
import useIntlMessage from "hooks/useIntlMessage";
import { ENTER_TEXT } from "../../../../constants/CommonConstants";
import { useSelector } from "react-redux";
import { STATUSES } from "../../../../constants/ManageEmployeeConstants";
import { EMP_FORM_FIELDS } from "constants/ManageEmployeeConstants";
import { DESIGNATION_STATUSES } from "../../../../constants/ManageDesinations";
import { UPDATE_TEXT } from "../../../../constants/ManageAccounts";
import { DOMAIN_MESSAGES } from "constants/AddDomainFormConstant";

const AddDesignationForm = ({ setIsVisible, onClose, onSuccess, data }) => {
  const [form] = Form.useForm();
  const { confirm } = Modal;
  const [error, setError] = useState("");
  const { Text } = Typography;
  const getMessage = useIntlMessage();
  const [loading, setloading] = useState(false);
  const { Option } = Select;
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  // const CountryList = ["Canada", "Chile", "Colombia", "India", "Pakistan", "United State"];

  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onClose();
      },
    });
  };
  const validateField = response => {
    if (response?.statusCode === 201) {
      form.resetFields();
      message.success(SUCCESS_MESSAGE.value);
      onSuccess();
      onClose();
    }
  };
  const onCreateDesignation = async (data, designationId) => {
    try {
      setloading(true);
      await designationService.createDesignation(data, designationId);
      message.success(SUCCESS_MESSAGE.value);
      onClose();
      onSuccess();
    } catch (err) {
      validateField(err);
    } finally {
      setloading(false);
    }
  };
  const onUpdateDesignation = async data => {
    try {
      setloading(true);
      const payload = { designation: data?.designation, country: data?.country, activeInd: data?.status };
      await designationService.updateDesignation(payload);
      message.success(UPDATE_SUCCESS.value);
      onClose();
      onSuccess();
    } catch (err) {
      validateField(err);
    } finally {
      setloading(false);
    }
  };
  const handleOk = () => {
    if (!form.isFieldsTouched()) {
      setError(DOMAIN_MESSAGES.NO_CHANGES_TO_SAVE);
      return;
    }
    form
      .validateFields()
      .then(values => {
        if (data) {
          onUpdateDesignation({ ...values });
        } //else onCreateDesignation({ ...values, designationId: data?.designationId });
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const handleCancel = () => {
    if (form.isFieldsTouched()) {
      handleConfirm();
    } else {
      onClose();
    }
  };
  const handleChange = event => {
    if (error) {
      setError("");
    }
  };
  const isEditMode = React.useCallback(() => {
    return data && true;
  }, [data]);
  const initialValues = {
    country: data?.country,
    designation: data?.designation,
    status: data?.activeInd,
  };
  return (
    <Modal
      visible={setIsVisible}
      title={data ? UPDATE_DESIGNATION.text : ADD_DESIGNATION.text}
      onCancel={handleCancel}
      onOk={handleOk}
      okText={data ? UPDATE_TEXT.text : ADD_TEXT.text}
      okButtonProps={loading}
    >
      <Form
        form={form}
        layout={FORM_LAYOUT.horizontal}
        name={FORM_NAME.text}
        labelAlign="left"
        {...formItemLayout}
        initialValues={initialValues}
      >
        <Form.Item
          name={FILTER_FORM_FIELDS_OF_DESIGNATION.CITY.name}
          label={getMessage(FILTER_FORM_FIELDS_OF_DESIGNATION.CITY.label)}
          rules={[{ required: isEditMode() ? false : true }]}
        >
          {isEditMode() ? (
            <span className="profile-text-color">{data?.country}</span>
          ) : (
            <Select
              placeholder={getMessage(
                FILTER_FORM_FIELDS_OF_DESIGNATION.CITY.label,
                FILTER_FORM_FIELDS_OF_DESIGNATION.CITY.label,
              )}
              name={FILTER_FORM_FIELDS_OF_DESIGNATION.CITY.name}
              form={form}
              //  disabled={isEditMode()}
            >
              {allLocations?.map(e => {
                return (
                  <Option key={e?.country} value={e?.country}>
                    {e?.country}
                  </Option>
                );
              })}
            </Select>
          )}
        </Form.Item>
        <Form.Item
          name={DESIGNATION_FORM_FIELDS.DESIGNATION_FIELD.value}
          label={getMessage(DESIGNATION_FORM_FIELDS.DESIGNATION_FIELD.label)}
          rules={[
            {
              required: isEditMode() ? false : true,
            },
          ]}
        >
          {isEditMode() ? (
            <span className="profile-text-color">{data?.designation}</span>
          ) : (
            <Input
              //  disabled={isEditMode()}
              onChange={handleChange}
              placeholder={getMessage(ENTER_TEXT.label, DESIGNATION_FORM_FIELDS.DESIGNATION_FIELD.label)}
            />
          )}
        </Form.Item>
        <Form.Item
          name={DESIGNATION_FORM_FIELDS.DESIGNATION_STATUS.name}
          label={getMessage(DESIGNATION_FORM_FIELDS.DESIGNATION_STATUS.label)}
          rules={[{ required: true }]}
        >
          <Select placeholder={getMessage(EMP_FORM_FIELDS.EMP_STATUS.label)}>
            {Object.values(DESIGNATION_STATUSES).map(e => {
              return (
                <Option key={e.name} value={e.value}>
                  {getMessage(e.name)}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        {error && <div style={{ color: "red" }}>* {error}</div>}
      </Form>
    </Modal>
  );
};

export default AddDesignationForm;
