import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import { domainService } from "services/DomainService";

const designationService = {};

designationService.deleteDesignation = async function (designationId) {
  const { path, method } = API_CONSTANTS.DELETE_DESIGNATION;

  try {
    const response = await service({
      url: `${path}${designationId}`,
      method,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in deleting designation.", e);
    throw Error(e);
  }
};

designationService.getList = async function (params) {
  // const { path, method } = API_CONSTANTS.DESIGNATION_LIST;
  const { path, method } = API_CONSTANTS.DESIGNATION_LIST_BY_PARAMS;
  try {
    const response = await service({
      url: path,
      method,
      data: params,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    response.records?.forEach((record, index) => {
      record.key = index;
    });
    return response.records;
  } catch (e) {
    console.error("Error in getting the designation list.", e);
  }
};

designationService.createDesignation = async function (data) {
  const { path, method } = API_CONSTANTS.CREATE_DESIGNATION;
  try {
    const response = await service({
      url: path,
      method,
      data: data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in changing designation status.", e);
  }
};

designationService.changeStatus = async function (data = "", designationId) {
  const { path, method } = API_CONSTANTS.CHANGE_DESIGNATION_STATUS;
  try {
    const response = await service({
      url: `${path}${designationId}`,
      method,
      data,
      headers: {
        "content-type": "text/plain",
      },
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in changing designation status.", e);
  }
};

designationService.uploadDocument = async function (payload) {
  const { path, method } = API_CONSTANTS.UPLOAD_DESIGNATION;
  try {
    const response = await service({
      url: path,
      method,
      data: payload,
    });
    return response;
  } catch (error) {
    console.error("Something went wrong while uploading document", error);
    throw Error(error);
  }
};
designationService.updateDesignation = async function (data) {
  const { path, method } = API_CONSTANTS.UPLOAD_DESIGNATION_API;
  try {
    const response = await service({
      url: path,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in updating designation status.", e);
  }
};
export default designationService;
